import "chart.js/auto";
import { Chart } from "react-chartjs-2";
import store from "./Store2";
import "moment/locale/ru";
import { Card } from "antd";

function DonePlansChart({ date }: { date: string }) {
  let done = 0;
  function PrepareDataChart(date: string) {
    let expectedDur = 0;
    let realDur = 0;
    store.store.sortPlanner(date).map((action) => {
      const expectedDurArr = action.expectedDuration.split(":");
      expectedDur +=
        +expectedDurArr[0] * 3600 +
        +expectedDurArr[1] * 60 +
        +expectedDurArr[2];
      realDur += action.realDuration;
      return expectedDur && realDur;
    });
    done = (realDur * 100) / expectedDur;
    let unDone = 100 - done;
    if (realDur === 0) {
      unDone = 100;
    }
    return [done, unDone];
  }

  const data = PrepareDataChart(date);

  const chartData = {
    labels: ["Done (%)", "To be done (%)"],
    datasets: [
      {
        id: 1,
        label: `${store.store.actionCards.length}`,
        data,
        backgroundColor: ["rgba(255, 183, 3, 0.8)", "rgba(243, 246, 224, 1)"],
        borderColor: ["black", "black"],
        borderWidth: 0.2,
      },
    ],
  };

  return (
    <div>
      <Card
        style={{
          backgroundColor: "#023047",
          color: "rgba(255, 183, 3)",
          fontWeight: "bold",
          textAlign: "center",
        }}
      >
        <Chart
          datasetIdKey={`${store.store.actionCards.length}`}
          type="pie"
          data={chartData}
          options={{
            color: "rgba(255, 183, 3)",
            font: {
              family: "PTSansRegular",
            },
          }}
        />
        <p>
          Your tasks were completed by{" "}
          {isNaN(Math.floor(done)) ? "0" : Math.floor(done)}%
        </p>
      </Card>
    </div>
  );
}

export default DonePlansChart;
