import "chart.js/auto";
import { Chart } from "react-chartjs-2";
import store from "./Store2";
import { observer } from "mobx-react-lite";

function PlanningChart({ date }: { date: string }) {
  const chartData = {
    datasets: [
      {
        id: 1,
        label: `${store.store.actionCards.length}`, //changeId,
        data: store.store.preparePlanningChart(date),
        backgroundColor: [
          "rgba(142, 202, 230, 1)",
          "rgba(255, 183, 3, 0.8)",
          "rgba(255,255,255)",
        ],
        borderColor: ["black", "black"],
        borderWidth: 0.2,
      },
    ],
    labels: ["Work (mins)", "Leisure (mins)"],
  };

  return (
    <div className="chartPlanning">
      <Chart datasetIdKey={`${Math.random()}`} type="pie" data={chartData} />
      <p>Time allocation</p>
    </div>
  );
}

export default observer(PlanningChart);
